<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
      <el-form label-position="top" label-width="auto" size='mini'>
        <el-form-item label="页面标题">
          <el-input placeholder="请输入页面标题" v-model="title"></el-input>
        </el-form-item>
        <el-form-item label="app选择">
          <el-select v-model="select" placeholder="请选择">
            <el-option label="ebook-H5" value="zhaoxitech://com.meizu.media.ebook/website?title="></el-option>
            <el-option label="cbook-H5" value="zhaoxitech://com.zhaoxitech.cbook/website?title="></el-option>
            <el-option label="miaogeng-H5" value="zhaoxitech://com.zhaoxitech.miaogeng/website?title="></el-option>
            <el-option label="huawei-H5" value="zhaoxitech://com.zhaoxitech.cbook.huawei/website?title="></el-option>
            <el-option label="sanctpalace" value="sanctpalace://com.meizu.compaign/website?title="></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否允许刷新">
          <el-select v-model="enableRefresh" placeholder="请选择">
            <el-option label="允许刷新" value='1'></el-option>
            <el-option label="不许刷新" value='2'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="H5页面路径">
          <el-input type='textarea' :row='2' placeholder="请输入H5页面路径" v-model="url"></el-input>
        </el-form-item>
        <el-form-item label="scheme路径">
          <el-input type='textarea' :row='4' v-model="result" placeholder='此处内容可由上面的H5页面路径填写后生成' readonly="readonly"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleBtn">立即跳转</el-button>
          <el-button @click='reset'>重置</el-button>
        </el-form-item>
      </el-form>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="98%">
        <div>https://zxbook-res.zhaoxitech.com/cbook_h5/test/pages/android-api-test.html?code=console.log(NativeUtil);NativeUtil.getHeaderInfo()</div>
        <br>
        <div>https://zxbook-res.zhaoxitech.com/cbook_h5/credits-shop/pages/index.html?id=41&source=test&__inject_vconsole__=1</div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false">关闭</el-button>
        </span>
      </el-dialog>
      <el-button type="text" @click="dialogVisible = true" style='position:fixed;bottom:0;right:0;opacity:0.01'>点击打开 Dialog</el-button>
  </div>
</template>

<script>
import { Input, Select } from 'element-ui'
const UTILS_JUMP_STORAGE_KEY = 'utils_jump_storage_key'

export default {
  name: 'App',
  components: {
    Input,
    Select
  },
  computed: {
    result () {
      return this.getResult()
    }
  },
  data () {
    var val = localStorage.getItem(UTILS_JUMP_STORAGE_KEY)
    return {
      dialogVisible: false,
      title: 'test',
      url: '',
      enableRefresh: '1',
      select: val || 'zhaoxitech://com.meizu.media.ebook/website?title='
    }
  },
  methods: {
    getResult () {
      return `${this.select}${window.encodeURIComponent(this.title)}&enableRefresh=${this.enableRefresh === '1'}&url=${window.encodeURIComponent(this.url)}`
    },
    handleBtn () {
      location.href = this.getResult()
    },
    reset () {
      this.url = ''
    }
  },
  watch: {
    select (val) {
      localStorage.setItem(UTILS_JUMP_STORAGE_KEY, val)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.el-select .el-input {
  width: 150px;
}
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
  margin-bottom: 10px !important;
}
.el-form--label-top .el-form-item__label{
  padding-bottom: 0px !important;
}
</style>



